import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/services/user.service';
import { ToastrService } from 'ngx-toastr';
import { ClientLayoutService } from '../../client-layout.service';

@Component({
  selector: 'app-client-profile',
  templateUrl: './client-profile.component.html',
  styleUrls: ['./client-profile.component.css']
})
export class ClientProfileComponent implements OnInit {


  user: any;

  defaultPasswords = {
    password: '',
    new_password: '',
    confirm_password: ''
  };

  passwords = { ...this.defaultPasswords };

  constructor(
    private userService: UserService,
    private clientLayoutService: ClientLayoutService,
    private toastrService: ToastrService
  ) { }

  ngOnInit(): void {
    this.clientLayoutService.setPageTitle('Profile');
    const current_user = JSON.parse(localStorage.getItem('user'));
    this.userService.getClient(current_user.client.id).subscribe(
      data => this.user = data
    );
  }

  updatePassword() {
    this.userService.updateClientPassword(this.user.data.id, this.passwords).subscribe(
      data => {
        if (data.status === 'success') {
          this.toastrService.success(data.message);
          this.auth();
        } else {
          this.toastrService.error(data.message);
        }
        this.passwords = { ...this.defaultPasswords };
      }
    );
  }

  updateData() {
    this.userService.updateClient(this.user.data.id, this.user.data).subscribe(
      data => {
        if (data.status === 'success') {
          console.log(data);
          this.user = data;
          this.toastrService.success(data.message);
          this.auth();
        } else {
          this.toastrService.error(data.message);
        }
      }
    );
  }

  uploadImageInput(event: any) {
    const reader = new FileReader();
    const file = event.target.files[0];
    let ext = file.name.split('.');
    ext = ext[ext.length - 1];

    reader.onloadend = () => {
        const img = reader.result.toString().split(',')[1];
        const uploadData = {
            type: ext,
            img
        };
        this.userService.updateClientAvatar(this.user.data.id, uploadData)
        .subscribe(data => {
          if (data.status === 'success') {
            this.toastrService.success(data.message);
            this.user = data;
            this.auth();
          } else {
            this.toastrService.error(data.message);
          }
        });
    };
    reader.readAsDataURL(file);
  }

  auth() {
    this.userService.auth().subscribe(
      data => {
      if (data.status === 'success') {
        this.userService.setUser(data.data);
      }
    });
  }

}
