import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/services/user.service';
import { ClientLayoutService } from '../../client-layout.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-activate-pin',
  templateUrl: './activate-pin.component.html',
  styleUrls: ['./activate-pin.component.css']
})
export class ActivatePinComponent implements OnInit {
  pin: string;
  constructor(
    private userService: UserService,
    private clientLayoutService: ClientLayoutService,
    private toastrService: ToastrService
  ) { }

  ngOnInit(): void {
  }

  activateAccount() {
    this.userService.usePin({ 'pin': this.pin }).subscribe(
      data => {
        if (data.status === 'success') {
          this.toastrService.success(data.message);
          this.clientLayoutService.client.next(data.data);
        } else {
          this.toastrService.error(data.message);
        }
      }
    );
  }

}
