import { Component, OnInit } from '@angular/core';
import { ClientLayoutService } from '../../client-layout.service';
import { UserService } from 'src/app/services/user.service';

@Component({
  selector: 'app-client-index',
  templateUrl: './client-index.component.html',
  styleUrls: ['./client-index.component.css']
})
export class ClientIndexComponent implements OnInit {

  referralLink = '';

  constructor(
    public clientLayoutService: ClientLayoutService,
    private userService: UserService
  ) { }

  ngOnInit(): void {
    this.referralLink = `${window.location.origin}/register?id=${this.userService.encryptEmail(this.clientLayoutService.client.value.email)}`;
    console.log(this.referralLink);
  }

}
